import keys from '../../keys'

export const displayMap = (locations) => {
    mapboxgl.accessToken = keys.mapboxKey

    var map = new mapboxgl.Map({
        container: 'map',
        style: 'mapbox://styles/beshoux/cllcex03f00v501qp5np499pc',
        scrollZoom:false
    });

    /** This will send all the tours locations to the map and make it adapt it's size to show all of them at the same time */
    const bounds = new mapboxgl.LngLatBounds()

    locations.forEach(loc => {
        /* Create marker */
        const el = document.createElement('div');
        el.className = 'marker';

        /* Add marker */
        new mapboxgl.Marker({
            element: el,
            anchor: 'bottom'
        }).setLngLat(loc.coordinates).addTo(map)

        /* Add popup */
        new mapboxgl.Popup({
            offset: 30
        }).setLngLat(loc.coordinates).setHTML(`<p> Day ${loc.day}: ${loc.description}</p>`).addTo(map)

        /* Extend map bounds to include current location */
        bounds.extend(loc.coordinates)
    })

    map.fitBounds(bounds, {
        padding: {
            top: 200,
            bottom: 150,
            left: 100,
            right: 100
        }
    })    
}